@import "partials/_variables";

/********************************************************
			Right to Left Styles
********************************************************/
//<992
@media only screen and (max-width: $st-screen-md-max) {
    #footer .title_block{
        padding: 10px 10px 10px 35px !important;
    }
    #footer{
      .footer_block_content{
        padding-left: 0;
        padding-right: 10px;
      }
    }
}
//<768
@media only screen and (max-width: $st-screen-sm-max) {
 
  .is_rtl .product_list.list{
    .blog_lr{
      .pro_first_box{
        float: none;
      }
      .pro_second_box{
        margin-left: 0;
      }
    }
    .ajax_block_product{
      .pro_first_box{
        float: none;
      }
      .pro_second_box{
        margin-left: 0!important;
      }
    }
  }

  .product_list.grid, .products_sldier_swiper {
    .blog_lr{
        .pro_first_box{
          float: none;
          margin-bottom: 10px;
        }
        .pro_second_box{
          margin-left: 0;   
        }
    }
  }
  

}

